import React, { Component } from 'react';
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import CssBaseline from '@material-ui/core/CssBaseline';
import useScrollTrigger from '@material-ui/core/useScrollTrigger';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import Slide from '@material-ui/core/Slide';

import Button from '@material-ui/core/Button';

import SelectBook from './SelectBook.js'
import './App.css';

function HideOnScroll(props) {
  const { children, window } = props;
  // Note that you normally won't need to set the window ref as useScrollTrigger
  // will default to window.
  // This is only being set here because the demo is in an iframe.
  const trigger = useScrollTrigger({ target: window ? window() : undefined });

  return (
    <Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>
  );
}

HideOnScroll.propTypes = {
  children: PropTypes.element.isRequired,
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
};

class App extends Component {
    constructor(props){
      super(props)
    }

  ScrollTop() {
    // Note that you normally won't need to set the window ref as useScrollTrigger
    // will default to window.
    // This is only being set here because the demo is in an iframe.
    console.log("ScrollTop");

    const anchor = document.querySelector('#back-to-top-anchor');

    if (anchor) {
      anchor.scrollIntoView({ block: "start", inline: "start" });
    }
  }
  
  // ver 1
  getBible(ver1,ver2, book, chapter){
    var xhr = new XMLHttpRequest();
    xhr.open("GET", "json/"+ver1+"_"+book+"_"+chapter +".json", true);
    xhr.onload = function (e) {
      if (xhr.readyState === 4) {
        console.log('4');
        if (xhr.status === 200) {
          console.log('200');
          //console.log(xhr.responseText);
          var json_obj1 = JSON.parse(xhr.responseText);
          this.setState({
            ver1 : ver1,
            ver2 : ver2,
            book : book,
            chapter : chapter,
            json1 : json_obj1,
            json2 : this.state.json2,
            selectBook : this.state.selectBook,             
            selectChapter : this.state.selectChapter
          });

          this.getBible2(ver1,ver2, book, chapter);
        } else {
          console.error(xhr.statusText);
        }
      }
    }.bind(this);
    xhr.onerror = function (e) {
      console.error(xhr.statusText);
    };
    xhr.send(null);        
  }

  getBible2(ver1,ver2, book, chapter){
    // ver 2
    var xhr2 = new XMLHttpRequest();
    xhr2.open("GET", "json/"+ver2+"_"+book+"_"+chapter +".json", true);
    xhr2.onload = function (e) {
      if (xhr2.readyState === 4) {
        console.log('4');
        if (xhr2.status === 200) {
          console.log('200');
          //console.log(xhr2.responseText);
          var json_obj2 = JSON.parse(xhr2.responseText);

          this.setState({
            ver1 : ver1,
            ver2 : ver2,
            book : book,
            chapter : chapter,
            json1 : this.state.json1,
            json2 : json_obj2,
            selectBook : this.state.selectBook,             
            selectChapter : this.state.selectChapter
          });
          this.saveState(ver1, ver2, book, chapter);
          
        } else {
          console.error(xhr2.statusText);
        }
      }
    }.bind(this);
    xhr2.onerror = function (e) {
      console.error(xhr2.statusText);
    };
    xhr2.send(null);      
  }
  
  componentWillMount() {
      console.log('componentWillMount');
      this.loadState();

      if(!localStorage.getItem("ver1")) {
        // default
        console.log("default");
        this.getBible('NLT','RNKSV','GEN','1'); 
      } else {
        // get previous
        console.log("get previous");
        this.getBible(localStorage.getItem("ver1"),localStorage.getItem("ver2"),
        localStorage.getItem("book"),localStorage.getItem("chapter")); 
      }
  }

  saveState(ver1, ver2, book, chapter){
    console.log("saveState");
    localStorage.setItem("ver1",ver1);
    localStorage.setItem("ver2",ver2);
    localStorage.setItem("book",book);
    localStorage.setItem("chapter",chapter);

    console.log("this.state.ver1 + "+localStorage.getItem("ver1"));
  }

  loadState(){
    console.log("loadState");
    this.setState({
      ver1 : localStorage.getItem("ver1"),
      ver2 : localStorage.getItem("ver2"),
      book : localStorage.getItem("book"),
      chapter : localStorage.getItem("chapter"),
      json1 : '',
      json2 : '',
      selectBook : false,             
      selectChapter : false
    });

    console.log("this.state.ver1 + "+this.setState.ver1);
  }
  
  clickPrevNext(isNext){
    console.log('clickPrevNext : '+isNext);
    if(isNext){
      var i = parseInt(this.state.chapter);

      const max_chapter = ["50","40","27","36","34","24","21","4","31","24","22","25","29","36","10","13","10","42","150","31","12","8","66","52","5","48","12","14","3","9","1","4","7","3","3","3","2","14","4","28","16","24","21","28","16","16","13","6","6","4","4","5","3","6","4","3","1","13","5","5","3","5","1","1","1","22"];
      const BOOK_LIST = ["GEN","EXO","LEV","NUM","DEU","JOS","JDG","RUT","1SA","2SA","1KI","2KI","1CH","2CH","EZR","NEH","EST","JOB","PSA","PRO","ECC","SNG","ISA","JER","LAM","EZK","DAN","HOS","JOL","AMO","OBA","JON","MIC","NAM","HAB","ZEP","HAG","ZEC","MAL","MAT","MRK","LUK","JHN","ACT","ROM","1CO","2CO","GAL","EPH","PHP","COL","1TH","2TH","1TI","2TI","TIT","PHM","HEB","JAS","1PE","1PE","1JN","2JN","3JN","JUD","REV"];

      console.log("this.state.book + "+this.state.book);
      console.log("indexOf + "+BOOK_LIST.indexOf(this.state.book));
      console.log("this.max_chapter + "+max_chapter[BOOK_LIST.indexOf(this.state.book)]);

      if(i<max_chapter[BOOK_LIST.indexOf(this.state.book)]) {
        i++;
        
        this.getBible(this.state.ver1,this.state.ver2, this.state.book, i);
        this.ScrollTop();
      }
    }
    else{
      var i = parseInt(this.state.chapter);
      if(i>1) {
        i--;
        
        this.getBible(this.state.ver1,this.state.ver2, this.state.book, i);
        this.ScrollTop();
      }
    }
  }

  clickBook(){
    console.log("clickBook");
   
    this.setState({
      ver1 : this.state.ver1,
      ver2 : this.state.ver2,
      book : this.state.book,
      chapter : this.state.chapter,
      json1 : this.state.json1,
      json2 : this.state.json2,
      selectBook : true,             
      selectChapter : false
    });
  }
  
  selectBook = (info) => {
    console.log("selectBook : "+ info);

    if(!!info) {
      this.setState({
        ver1 : this.state.ver1,
        ver2 : this.state.ver2,
        book : info,
        chapter : '1',
        json1 : this.state.json1,
        json2 : this.state.json2,
        selectBook : false,             
        selectChapter : false
      });
      this.getBible(this.state.ver1,this.state.ver2, info, '1');
      this.ScrollTop();
    }

    //this.setState({modal: !this.state.modal}) // true/false toggle
  }

  render(props) {
    console.log('render !!');
    

    try{
      var json1 = this.state.json1;
      //console.log('json1 + '+JSON.stringify(json1));
      var json2 = this.state.json2;
      //console.log('json2 + '+JSON.stringify(json2));
    }catch(error){}

    var list = "";
    if(!!json1 && !!json2) {
      var obj1 = Object.keys(json1);
      //console.log("length : "+ json2["verses"].length);
      list = obj1.map((item) =>
        json1[item].map(function(item,index) { 
          var title2 = "";
          var vers = "";
          //console.log("index : "+index);
          if(index < json2["verses"].length){
            if(json2["verses"][index].includes('<h>')) {
              title2 = json2["verses"][index].substring(json2["verses"][index].indexOf('<h>')+3,json2["verses"][index].indexOf('</h>'));
              vers = json2["verses"][index].substring(json2["verses"][index].indexOf('</h>')+4,json2["verses"][index].length);
            }
            else {
              vers = json2["verses"][index];
            }
          }

          if(item.includes('<h>')){

            return <div>
                  <br/>
                  <b>{item.substring(item.indexOf('<h>')+3,item.indexOf('</h>'))}<br/>
                  {title2}
                  </b>
                  <br/>

                  {index+1}. {item.substring(item.indexOf('</h>')+4,item.length)}<br/>
                  
                  &nbsp;&nbsp;&nbsp;&nbsp;{vers}
                  </div>;
          }
            
          return <div>
                    <br/>
                    {index+1}. {item}<br/>
                    &nbsp;&nbsp;&nbsp;&nbsp;{vers}
                  </div>;
        })
      )
    }

    return (
      <React.Fragment >
        <CssBaseline  />
        <HideOnScroll {...props} >
          <AppBar>
            <Toolbar >
              <Button variant="contained" onClick={() => {this.clickBook();}}>{!!this.state.book ? this.state.book : ''}</Button>
              <Button variant="contained" onClick={() => { this.clickPrevNext(false); }}>{'<<'}</Button>
              <Button variant="contained">{!!this.state.chapter ?this.state.chapter : ''}</Button>
              <Button variant="contained" onClick={() => { this.clickPrevNext(true); }}>{'>>'}</Button>
            </Toolbar>
          </AppBar>
        </HideOnScroll>
        <Container id="back-to-top-anchor">
        <Toolbar />
          <Box my={2} >
            {list}
          </Box>
        </Container>
        <SelectBook displayModal={this.state.selectBook}
                 closeModal={this.selectBook}
                 isOld ={this.BOOKS}/>
      </React.Fragment>
    );
  }
}

export default App;